.league-edit__form .form__group {
  margin-bottom: 20px; }
  .league-edit__form .form__group .label {
    height: 32px;
    line-height: 32px; }
    .league-edit__form .form__group .label.require .label-name:before {
      content: '*';
      color: red; }
  .league-edit__form .form__group .label-name {
    display: inline-block;
    width: 20%;
    padding-right: 5px;
    text-align: right; }
  .league-edit__form .form__group .input {
    display: inline-block;
    width: 75%;
    height: 34px;
    outline: 0;
    padding: 0 8px;
    border: 1px solid #d9d9d9;
    border-radius: 5px;
    vertical-align: middle; }
    .league-edit__form .form__group .input:focus {
      border-color: #40a9ff; }
    .league-edit__form .form__group .input.lack {
      border-color: #f44336; }
  .league-edit__form .form__group .file_input {
    position: absolute;
    margin-left: 15px;
    left: 20%;
    opacity: 0; }

.float-right {
  float: right; }

.clearfix:before {
  content: "";
  display: block;
  clear: both; }
