.ui-pagination {
  text-align: center; }

.ui-pagination-list {
  display: inline-block; }

.ui-pagination-item {
  position: relative;
  display: inline-block;
  border-radius: 4px;
  user-select: none;
  min-width: 32px;
  height: 32px;
  line-height: 30px;
  text-align: center;
  list-style: none;
  vertical-align: middle;
  border: 1px solid #d9d9d9;
  background-color: #fff;
  margin-right: 8px;
  font-family: Arial;
  outline: 0;
  cursor: pointer; }

.ui-pagination-item.prev a {
  display: inline-block;
  width: 18px;
  height: 18px;
  background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABIAAAASCAYAAABWzo5XAAAAnElEQVQ4T9XSMQ7CMAwFUFvOXTqwcJFEyUFYWNngDgWJjamb4xExABdzUNeKNgF5gKxWnvS/jWD00MiBP4JSSidVvYoIt8SfjRZjXAPADRE3zDzUsMWOvPcrInoi4paZL0tYtewRc87dAWDHzOc5rAqNH0MIHRE9Sin7nPPxHfYRpKoHEem/gkyimZRttn6zg6wd4HTetLUW9PegF9bnPhOJZWsRAAAAAElFTkSuQmCC) no-repeat;
  vertical-align: middle;
  background-size: contain;
  transform: rotate(180deg); }

.ui-pagination-item.next a {
  display: inline-block;
  width: 18px;
  height: 18px;
  background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABIAAAASCAYAAABWzo5XAAAAnElEQVQ4T9XSMQ7CMAwFUFvOXTqwcJFEyUFYWNngDgWJjamb4xExABdzUNeKNgF5gKxWnvS/jWD00MiBP4JSSidVvYoIt8SfjRZjXAPADRE3zDzUsMWOvPcrInoi4paZL0tYtewRc87dAWDHzOc5rAqNH0MIHRE9Sin7nPPxHfYRpKoHEem/gkyimZRttn6zg6wd4HTetLUW9PegF9bnPhOJZWsRAAAAAElFTkSuQmCC) no-repeat;
  background-size: contain;
  vertical-align: middle; }

.ui-pagination-item:hover,
.ui-pagination-item.active {
  transition: all .3s;
  border-color: #1890ff; }

.ui-pagination-item:hover a,
.ui-pagination-item.active a {
  color: #1890ff; }

.ant-pagination-item-link {
  font-size: 14px;
  color: rgba(0, 0, 0, 0.65); }
